import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController, NavParams, ViewController, TextInput } from 'ionic-angular';
import { User } from '../../models/user';

@Component({
  selector: 'page-editProfile',
  templateUrl: 'editProfile.html',
})
export class EditProfilePage {
  @ViewChild('bio') bio: ElementRef;
  user = {} as User;

  constructor(public navCtrl: NavController,
              private navParams: NavParams,
              private view: ViewController) {
  }

  ionViewWillEnter() {
    this.user = JSON.parse(this.navParams.get('user'));
  }

  resize() {
    this.bio.nativeElement.style.height = this.user.bio.nativeElement.scrollHeight + 'px';
  }

  closeModal() {
    this.view.dismiss();
  }
}



